import React, { useState } from "react"
import { NavLink, useNavigate } from "react-router-dom";
import logo from "./tool.png";
import styled from "styled-components";
import { Button } from "antd";
import { observer } from "mobx-react";
import useStores from "../stores";
import { Auth } from "../models";

const StyImg = styled.img`
  height: 30px;
`;

const StyHeader = styled.header`
  display: flex;
  background-color: #031529;
  align-items: center;
  padding: 10px 50px;
`;

const StyNavLink = styled(NavLink)`
  color: #f2f3f4;
  margin-left: 30px;
  &.active {
    border-bottom: 1px solid #f2f3f4;
  }
  text-decoration: none;
`;

const StyLogin = styled.div`
  margin-left: auto;
  color: #f2f3f4;
`;

const StyButton = styled(Button)`
  margin-left: 10px;
`;

const Header = observer(() => {
  const {UserStore, ImageStore, HistoryStore} = useStores();
  const navigate = useNavigate();

  function handleLogout() {
    Auth.logout();
    UserStore.resetUser();
    ImageStore.serverFile = null;
    HistoryStore.reset();
  }

  function handleLogin() {
    navigate("/login");
  }

  function handleRegister() {
    navigate("/register");
  }

  return (
    <StyHeader>
      <StyImg src={logo}/>
      <nav>
        <StyNavLink to="/" className={(navData) => navData.isActive? "active": "none"}>主页</StyNavLink>
        <StyNavLink to="/history" className={(navData) => navData.isActive? "active": "none"}>上传历史</StyNavLink>
        <StyNavLink to="/about" className={(navData) => navData.isActive? "active": "none"}>关于本站</StyNavLink>
      </nav>
      <StyLogin>
        {
          UserStore.currentUser? <>
            {UserStore.currentUser.attributes.username} <StyButton type="primary" onClick={handleLogout}>注销</StyButton>
          </>: <>
            <StyButton type="primary" onClick={handleLogin}>登录</StyButton>
            <StyButton type="primary" onClick={handleRegister}>注册</StyButton>
          </>
        }
      </StyLogin>
    </StyHeader>
  );
});


export default Header;