import { makeObservable, observable, action } from "mobx";

class UserStore {
  constructor() {
    makeObservable(this);
  }

  @observable currentUser = null;

  @action pullUser(currentUser) {
    this.currentUser = currentUser;
  }

  @action resetUser() {
    this.currentUser = null;
  }

}

export default new UserStore();